	module.exports = {"data":{"site":{"siteMetadata":{"title":"Voracious Bookworm","author":"Lei"}},"markdownRemark":{"id":"/home/lei-lo/Code/voraciouscoder.github.io/src/pages/reading-roundup-21/index.md absPath of file >>> MarkdownRemark","html":"<p>Another trip derailed me this week! My days were wonderfully full, and I got to discover a bit of the diverse city of Toronto (never pronounced with the second t) &#x3C;3</p>\n<p><strong>Back Talk</strong><br>\n<em>Goodreads Summer Reading Challenge: Won’t Be Long</em></p>\n<p>Sixteen powerful stories packed into one book! I was amazed at the variety in characters. I loved reading stories about women of all different ages, flaws and strengths. It was the perfect book to bring on a work trip because I could easily read one story in downtime and not lose context.</p>","frontmatter":{"title":"Reading Roundup 21","date":"July 31, 2018"}}},"pathContext":{"slug":"/reading-roundup-21/","previous":{"fields":{"slug":"/reading-roundup-20/"},"frontmatter":{"title":"Reading Roundup 20"}},"next":{"fields":{"slug":"/reading-roundup-22/"},"frontmatter":{"title":"Reading Roundup 22"}}}}

